      variable: '--font-size-100',
      mobile: 17,
      desktop: 14,
    }, {
      name: 'Font size 200',
      variable: '--font-size-200',
      mobile: 19,
      desktop: 16,
    }, {
      name: 'Font size 300',
      variable: '--font-size-300',
      mobile: 22,
      desktop: 18,
    }, {
